import { useParams, useRouter } from 'next/navigation';
import { Share2, ArrowLeft, CalendarDays, X, Flame, Clock, MapPin, Users, MessageCircle, Lightbulb, ArrowLeftRight, FileDown, RotateCcw, Building2 } from 'lucide-react';
